.App{
  min-height:100vh;
  background-image: linear-gradient(to right, #eea2a2 0%, #bbc1bf 19%, #57c6e1 42%, #b49fda 79%, #7ac5d8 100%);}

.search-form{
  min-height:10vh;
  display:flex;
  justify-content: center;
  align-items:center;
}

.search-bar{
  width:50%;
 border:1px;
 padding:10px;
 background-color:rgb(231, 231, 231);
}

.search-button{
 background: lightcoral;
 border: none;
 padding:10px 20px;
 color:white;
}

.recipes{
  display:flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap:wrap;
 
}
